
.game {
  width: 100%;
  display: grid;
  place-items: center;
}
.status{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color:#2323AA;
}

.game-board {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.game-info {
  margin-top: 0px;
  /* width: 100%; */
  display: flex;
  justify-content: center;
  align-items: center;
}

#reset{
 width: 80px;
 height: 40px;
 font-family: 'Play', sans-serif;
 font-size: 20px;
 background-color: aquamarine;
 border-radius: 0.5rem;
}

#gamehistory{

}

#goto{
  font-family: 'Play', sans-serif;
  font-size: 15px;
  width: 150px;
  height: 30px;
  background-color:#c7e6dd;
  border-radius: 0.5rem;
  /* border: 13px;
  border-color: black; */
}
.moves{
  
  display: block;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 30px;
  font-size: 14px;
 }